header{
    margin-top: 0;
    background-color: rgba(0, 0, 0, 0);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    vertical-align: middle;
    position: fixed;
    top: 0;
    left: 0;
    height: 10vh;
    width: 100%;
    overflow: hidden;
    padding-top: 15px;
    position: fixed;
    z-index: 9999;
    margin: 0;
}
header img{
    width: 45%;
    cursor: pointer;
    padding-left: 25px;
    animation: fadeInFromNone 0.5s ease-in-out;
    transition: all .5s ease-in-out;
    cursor: none;
}

@keyframes fadeInFromNone {
    0% {
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

header ol{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    margin-right: 25px;
}

header ol li{
    margin:0 15px;
    color:var(--accent-color);
    font-weight: 400;
    cursor: none;
}
header ol li a{
    text-decoration: none;
    color:var(--accent-color);
    cursor: none;
}
.links img{
    display: none;
}
.linkList{
    animation: fadeOut 0.5s ease-in-out;
}
@keyframes fadeOut {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        opacity: .1;
    }

    100% {
        display: block;
        opacity: 1;
    }
}
.ham .linkList{
    display: none;
}

.ham img{
    display: block;
    width: 30%;
    cursor: none;
}
