.aboutComponent{
overflow: hidden;
}
.landing{
    margin: 0;
    padding: 0;
}

.landingPageTitle{
    margin: 0;
    color: white;
    font-size: 7.5rem;
    text-transform: lowercase;
    line-height: 95px;
    margin-top: 15px;
}
.landingPageTitle img{
    width: 50px;
    position: absolute;
    top:10px;
    right: 120px;
    transform: rotate(15deg);
    z-index: 2;
}
.flex{
    display: flex;
    flex-direction: row;
}
.landingPageTitle2{
    margin: 0;
    padding: 0;
    font-size: 7.5rem;
    line-height: 95px;
}
.flex img{
    margin-top: 0;
    width:150px;
    position: relative;
    z-index: 2;
}
.landingPageTitle2 span{
    margin-left: 45px;
}
.landingPageTitle2 img{
    margin: 0;
    width: 100px;
    position: relative;
    z-index: 2;
}


.cursorArea{
    margin: 5rem 10rem;
    width: 54rem;
    height: 31rem;
    overflow: hidden;
    position: relative;
}

.cursorIcon{
    margin: 0;
    padding: 0;
    position: absolute;
    top: -90px;
    left: -370px;
    will-change: top,left;
    z-index:1;
    transition: all .2s ease;
}
.cursorIcon img{
    margin: 0;
    padding: 0;
}

/** fuck finished */

section.details{
    display: flex;
    flex-direction: row;
    margin: 10rem 6rem;
    margin-bottom: 2rem;
    margin-top: 15rem;
}
.detailsPTag{
    margin-right: 8rem;
    font-size: 1.25rem;
}
.detailsAccent{
    margin-top: 2rem;
    color: var(--accent-color);
}

.detailsBGtag img{
    height: 85%;
}
/*The team */
.teamOfficeImgContainer{
    margin: 0;
    width: 100%;
    height: 30rem;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.teamOfficeImgContainer img{
    width: 100%;
    top: -100;
    left: 0;
    overflow: hidden;
    position: absolute;
    object-fit: cover;
}

section.team{
    margin: 8rem 6rem;
    position: relative;
}
#team {
    overflow: hidden;
}

.theTeam{
    margin: 0;
    padding: 0;
    transform: rotate(-90deg);
    position: absolute;
    top: 25px;
    left: -30px;;
    overflow: hidden;
}

.nasim{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: bottom;
}
.nasim .left{
    align-self: flex-end;
}

.teamContainer .nasim .name{
    margin: 0;
    padding: 0;
    font-size: 12rem;
    font-weight: 600;
    color: white;
    line-height: 5rem;
}


.nasim .left .nameDes{
    font-size: 6rem;
    margin: 0;
    padding: 0;
}
.nasim .left .nameSDes{
    font-size: 7.5rem;
    margin: 0;
    padding: 0;
}
.nasim .left .des{
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    font-size: 7rem;
    color: var(--accent-color);
    font-weight: 600;
    line-height: 5rem;
    margin-bottom: 8px;
}
.nasim .right{
    padding: 0;
    margin: 0;
    position: relative;
    overflow: hidden;
}
.hidden{
    display: none;
}

.teamContainer .middle{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.teamContainer .middle .left{
    width: 30rem;
    margin-top: 5rem;
    border: 1px solid var(--brown);
}
.teamContainer .middle .middle{
    position: relative;
    top: 2.5rem;
}

.teamContainer .middle .right{
    width: 30rem;
    margin-top: 5rem;
    border: 1px solid var(--brown);
}

.oyshi{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 5rem;
    text-align: right;
}
.oyshi .left{
    position: relative;
    overflow: hidden;
}

.oyshi .right .name{
    font-size: 12rem;
    font-weight: 600;
    line-height: 10rem;
    margin: 0;
    color: white;
    overflow: hidden;
}

.oyshi .des {
    text-transform: uppercase;
    font-size: 7rem;
    font-weight:600;
    margin: 0;
    padding: 0;
    line-height: 8rem;
    color: var(--accent-color);
}

.devider{
    width: 100vw;
    border: 1px solid var(--brown);
    margin: 5rem 0;
}

.codingStyle{
    margin: 0 5rem;
    margin-bottom:5rem ;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}
.codingStyle .left{
    flex: 1;
    overflow: hidden;
}
.codingS{
    color: var(--secondary-color);
    margin: 0;
    padding: 0;
}
.CSText{
    margin: 0;
    padding: 0;
    font-size: 4.5rem;
    font-weight: 600;
    line-height: 3.5rem;
    margin-left: 3rem;
    color: var(--accent-color);
    overflow: hidden;
}
.codingStyle .right{
    flex: 1;
    overflow: hidden;
}

.contactSmall{
    margin-bottom: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.contactSmall h1{
    color: var(--secondary-color);
    font-size: 2.4rem;
    margin-bottom: 0;
}
#contactSmall > div.mail{
    overflow: hidden;
}
#contactSmall > div.phone{
    overflow: hidden;
}
.contactSmall .mail{
    margin-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.mailDes p:nth-child(1){
    margin: 0;
    font-size: 1.5rem;
    color: var(--brown);
}
.mailDes p:nth-child(2){
    margin: 0;
    font-size: 2.5rem;
    color: var(--accent-color);
}
.contactSmall .phone{
    margin-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.contactCard{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30rem;
    margin-bottom: 10rem;
    position: relative;
}


section.contactCard .cardContainer{
    width: 650px;
}
section.contactCard .cardContainer .cardArrow{
    left: 630px;
}
@keyframes contactCardAnim {
    0%{
        display: none;
        opacity: 0;
    }
}
section.contactCard .c1{
    background-color: black;
    position: absolute;
    top:10%;
    left: 20%;
}

section.contactCard .c2{
    background-color: black;
    position: absolute;
    top:13%;
    left: 21%;
}
section.contactCard .c3{
    background-color: black;
    position: absolute;
    top:16%;
    left: 22%;
}
section.contactCard .c4{
    background-color: black;
    position: absolute;
    top:19%;
    left: 23%;
}
section.contactCard .cf{
    background-color: black;
    position: absolute;
    top:22%;
    left: 24%;
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 9;
}
.cf .imgAnimHideDiveBig{
    width: 650px;

}
.cf .imgAnimHideDiveBig p{
    margin: 1rem 4.5rem;
    font-size: 1.2rem;
}

.cf .imgAnimHideDiveBig a{
    position: relative;
    border: 3px solid var(--secondary-color);
    padding: .8rem;
    text-decoration: none;
    color: white;
    font-size: 1.8rem;
    font-weight: 600;
    animation-name: stretch;
    animation-duration: .25s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    animation-play-state: running;
    cursor: none;
}
.cf .imgAnimHideDiveBig a:hover{
    font-size: 1.8rem;
    font-weight: 600;
    color: black;
    background-color: var(--secondary-color);
    animation: none;
    opacity: 1;
}
@keyframes stretch {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}