.contactPageHero{
    margin-top: 10rem;
}

.contactForm {
    margin: 6.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.contactForm .left .contactFormDes{
    margin: 0;
    padding: 0;
    font-size: 2rem;
    font-weight: 600;
}

.contactForm .left .contactFormTitle{
    margin: 0;
    font-size: 6.5rem;
    font-weight: 600;
    line-height: 6rem;
    color: var(--accent-color);
}
.contactForm .left .contactFormDesBottom{
    margin: 0;
    font-size: 2rem;
    line-height: 5rem;
    color: var(--brown);
    font-weight: 600;
}

.contactForm .right form{
    display: flex;
    flex-direction: column;
}

.contactForm .right form input{
    border: 1px solid white;
    background-color: transparent;
    color: white;
    outline: none;
    margin-bottom: 10px;
    font-size: 1.5rem;
    padding: .5rem;
    width: 25rem;
}
.contactForm .right form input::placeholder{
    color: white;
}
.contactForm .right form textarea{
    border: 1px solid white;
    background-color: transparent;
    color: white;
    outline: none;
    margin-bottom: 10px;
    font-size: 1.2rem;
    padding: .5rem;
    width: 25rem;
    height: 15rem;

}
.contactForm .right form textarea::placeholder{
    color: white;
}
.contactForm .right form button{
    font-size: 1.5rem;
    font-weight: 600;
    padding: .5rem;
    width: 10rem;
    color: var(--accent-color);
    background-color: transparent;
    border: 1px solid var(--accent-color);
}
.contactForm .right form button:hover{
    color: black;
    background-color: var(--accent-color);
}
.contactPage{
    overflow: hidden;
    cursor: none;
    position: relative;
    z-index: 9999;
}
form{
    
    cursor: none;
}
#introContentRight > p:nth-child(1){
    padding-top: 25px;
}