:root{
  --accent-color : #8DFFA8;
  --secondary-color: #E1FF31;
  --gray:#D8D8D8;
  --brown:#A19E81;
}
html{
  font-size: 16px;
  scroll-behavior: smooth;
  cursor: url('./asset/cursorIcon.png') auto;
}
.App{
  background-color: #000000;
  color:#ffffff;
  font-family: 'Poppins', sans-serif;
}
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #000000;
}


/* Smartphones (portrait and landscape) ----------- */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  /* Styles */
  #nasim > div.left > p.nameSDes{
    font-size: 3.7rem;
}
  /* */
    #tsparticles{
      display: block;
    }
    #header{
      padding-top: 0;
    }
    #headerLogo img{
      width: 45px;
    }
    #header ol{
      margin-right: 0;
    }
    #header ol li a{
      font-size: 1.2rem;
    }
    #linkHam img{
      width: 45px;
      margin-right: 25px;
    }
    #hero{
      padding: 0  1.6rem;
      padding-top: 85px;
      padding: 0%;
    }
    #hero img{
      width: 80%;
      transform: none !important;
    }
    /* intro content */
    #intro{
      padding: 10px;
      padding-top: 35px;
      height: 100vh;
    }
    #intro h1{
      font-size: 4.5rem;
