    }

    #introContentLeft{
      margin: 0;
      width: auto;
      margin-left: 25px;
    }
    #introContentLeft p{
    margin: 0;
    }
    #introContent{
      flex-direction: column;
      margin: 0;
    }
    #introContentRight{
      width: auto;
    }
    #intro p{
      margin-right: 0;
    }
    /*Work*/
    #workSection{
      height: auto;
    }
    #workContainer{
      padding: 25px;
      grid-template-columns:1fr;
    }
    #cardContainer, #cardContainer2, #cardContainer3, #cardContainer4{
      width: 312px;
      height: 198px;
    }
    #cardArrow, #cardArrow2, #cardArrow3, #cardArrow4{
      left: 285px;
    }
    #cardImg{
      width: 312px;
    }
    #cardImg img{
      max-width: 312px;
    }
    #imgAnimHideDiveBig, #imgAnimHideDive{
      width: 312px !important;
      height: 150px !important;
    }

    /*Menu*/
   #menu a{
      font-size: 28px;
      color: #8DFFA8;
    }
    #menuWorkLink{
      padding-left: 25px;
    }
    #menuWorkLink p{
      font-size: 18px;
    }


    /*Dream*/
    #dream{
      height: 100vh;
      padding: 10px;
    }
    #dream h1{
      font-size: 4.5rem;
      line-height: 4rem;
      padding: 0;
      margin: 0;
    }
    #dreamContent{
      flex-direction: column;
      padding: 0;
      margin: 0;
      justify-content: start;
    }
    #dreamContentLeft{
      width: auto;
      margin: 0;
      margin-left: 25px;
    }
    #dreamContentRight{
      width: auto;
      margin: 25px;
    }
    #dreamContentRight p{
      margin: 0;
      margin-bottom: 25px;
    }
    /*Footer */
    #footerContainer{
      padding: 15px;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    #footerContainer p{
      font-size: 18px;
    }
    .logoName{
      margin: auto;
    }
    .logoName img{
      width: 125px;
      padding: 25px;
    }
    .copyright{
      padding: 25px;
    }
    .copyright p{
      right: 50%;
      transform: translate(50%, 50%);
    }
    /*Work Page*/
    #projectLog{
      margin: 25px;
      margin-top: 100px;
      flex-direction: column;
    }

    #PJleft h1{
      margin: 0;
      font-size: 4.5rem;
      line-height: 4rem;
    }
    #dLinkW{
      margin-top: 0;
      margin: 25px;
      font-size: 22px !important;
    }
    .detailLinks{
      font-size: 22px !important;
    }
    #dLinkW img{
      width: 8%;
    }

    .detailLinks img{
      width: 8% !important;
    }

