    }
    #landingPageTitle2 img{
      width: 50px;
      top: 0;
    }
    #cursorArea{
      margin: 75px 25px;
      width: 100vw;
    }

    #heroImgL{
      left: 0;
    }
    #details{
      flex-direction: column;
      padding: 0;
      margin: 25px;
    }
    #details img{
      display: none;
    }
    #detailsPTag{
      margin: 25px;
    }
    #teamOfficeImgContainer{
      height: 10rem;
    }
    #team{
      margin:25px;
    }
    #nasim{
      flex-direction: column;
      margin-top: 75px;
      align-items: center;
    }
    #nasim .left{
      margin-left: 25px;
      text-align: center;
      align-items: center;
      align-self: baseline;
    }
    #nasim .name{
      font-size: 6rem;
      line-height: 5rem;
    }
    #nasim .left .nameDes {
      font-size: 3rem;
      line-height: 2.5rem;
    }
    #nasim .left .des{
      font-size: 3.5rem;
    }
    #nasim img{
      margin-left: 25px;
      width: 90%;
    }

    #middle .middle{
      left: -10%;
    }
    #oyshi{
      flex-direction: column;
      margin-top: 75px;
      align-items: center;
    }

    #oyshi .right{
      margin-left: 25px;
      text-align: center;
      align-items: center;
      align-self: baseline;
    }
    #oyshi .name{
      font-size: 6rem;
      line-height: 5rem;
    }
    #oyshi .right .des{
      font-size: 3.7rem;
      line-height: 3rem;
    }
    #oyshi img{
      margin-left: 25px;
      width:110%;
    }
    
    #codingStyle{
