      margin: 25px;
    }
    #codingStyle .left{
      margin-right: 0;
      margin-bottom: 25px;
    }
    #codingStyle .CSText{
      font-size: 3.5rem;
      line-height: 3rem;
    }
    #contactSmall .mail{
