    }
    #contactSmall .mail img{
      width: 75px;
    }
    #contactSmall .mailDes p:nth-child(1){
      margin: 0;
      font-size: 1.5rem;
      color: var(--brown);
    }
    #contactSmall .mailDes p:nth-child(2){
        margin: 0;
        font-size: 1.5rem;
