      left: 0;
      border: none;
      margin: 10px;
    }
    #contactCard .cf .cardTitle{
      border: none;
    }
    #contactCard .cf .cardContainer{
      border: none;
    }
    #contactCard .cf .cardContainer .cardArrow{
