section.hero{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    color: var(--accent-color);
    margin: 1.6rem;
    padding: 2.2rem;
    position: relative;
    top: 0;
    left: 0;
    will-change: top,left;
    overflow: hidden;
    height: 100vh;
    cursor: none;
}
.mousePointerIcon{
    position: fixed;
    z-index: 99999;
    width: 20px;
    height: 20px;
    will-change: top, left;
    pointer-events: none;
    display: none;
}
.cursor{
    animation: cursorInAnim .5s;
    transform-origin: top left;
}
.cursorPointer{
    animation: cursorPAnim .5s;
    transform-origin: top left;
    width: 50px;
}
@keyframes cursorInAnim {
    0%{
        transform: rotate(25deg);
    }
    100%{
        transform: rotate(0);
    }
}
@keyframes cursorPAnim {
    0%{
        transform: rotate(-25deg);
    }
    100%{
        transform: rotate(0);
    }
}

section.hero .heroImg{
    margin-top: 4.8rem;
    width: 70%;
    position: relative;
    z-index: 1;
    cursor: none;
    transform: translateX(0%);
    transform: translateY(0%);

}   

section.hero p{
    color: #ffff;
    font-weight: 300;
    font-size: 1.2em;
    margin-bottom: 6.8rem;
}
.hiden{
    display: none;
}
