section.work{
    height: 880px;
    align-items: center;
    background-color: black;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.workContainer{
    padding: 100px 125px 0px 110px;
    width: 80%;
    display: grid;
    gap: 50px;
    grid-template-columns:1.8fr 1.2fr;
    align-items: center;
}

.cardContainer{
    border: 3px solid var(--secondary-color);
    position: relative;
    width: 520px;
    height: 330px;
    position: relative;
    display: grid;
    will-change: left, right, top, bottom;
}

.cardArrow{
    border: 2.5px solid rgb(255, 255, 49);
     opacity: 1; 
     transform: rotate(15deg);
     height: 45px;
     width: 45px;
     position: absolute;
     left:490px;
     top:-18px;
     background-color: black;
}

.cardArrow svg{
    width: 45px;
    height: 45px;
    transform: rotate(-45deg);
}
.cardTitle{
    margin: 0 0;
    border-bottom: 3px solid var(--secondary-color);
    padding:8px 20px;
    background-color: black;
    overflow: hidden;
}
#cardTitle{
    background: black;
}
.cardTitle p{
    text-transform: uppercase;
    margin: 0;
    font-size: 20px;
    font-weight: 600;
}
.cardImg img{
    max-width: 520px;
}
