    cursor: none;
}
.menuWorkLink1 span{
    font-size: 28px;
}
.menuWorkLink p{
    color: transparent;
    text-decoration: underline;
    padding-left: 25px;
    font-weight: 400;
    font-size: 1.2rem;
}

.menuWorkLink p img{
    display: none;
    filter: invert(82%) sepia(42%) saturate(415%) hue-rotate(74deg) brightness(102%) contrast(101%);
    width: 45%;
}
.menuWorkLink:hover .itsthisway{
   color: var(--accent-color);
}

.menuWorkLink:hover .itsthisway img{
    display: block;
}

.menuWorkLink::before{
    content: '';
    position: absolute;
    display: block;
    top: 0; right: 0; bottom: 0; left: 0;
    inset: 0 0 0 0;
    background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(141,255,168,1) 90%, rgba(225,255,49,1) 100%);    z-index: -1;
    transition: transform .3s ease;
    transform: scaleX(0);
    transform-origin: left;

}

.menuWorkLink:hover::before{
    color: var(--accent-color);
    transform: scaleX(1);
    transform-origin: bottom left;
}
