.footer{
    position: relative;
    z-index: 1;
    background-color: black;
}

.footerContainer{
    padding: 45px 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    z-index: 9999;

}


.footerContainer h3{
    text-transform: uppercase;
    font-size: 22px;
    color: white;
    margin-bottom: 0;
}
.footerContainer p{
    color: var(--secondary-color);
    font-size: 28px;
    font-weight: 600;
    margin: 0;
}
.socialIcons{
    display: grid;
    grid-gap:5px;
    grid-template-columns: 1fr 1fr;

}


.copyright p{
    margin: 0;
    text-align: center;
    padding-bottom: 25px;
    position: relative;
}